*{
  padding: 0px 0px;
  margin: 0px 0px;

}
body{
  background-color: rgb(216, 196, 196);
  display: flex;
  align-items: center;
  height:100dvh ;
  width: 100%;
}
form{
  height:95%;
  width: 80%;
  background-color: rgb(128, 170, 171);
  border-radius: 10px;
  border: none;
  outline: none;
  margin: auto;
}
#Name,#Email,#Phone-no,#Password{
  height: 5%;
  width: 36%;
  margin: 5% 5% 5% 5%;
  padding: 1% 1% 1% 1%;
  border-radius: 5px;
  border-width: 0px 0px 5px 0px;
  background-color: rgb(223, 205, 178);
  outline: none;
  text-align: end;
  font-size: 25px;
  font-weight:600 ;
  cursor: pointer;
}
#btn1{
  height: 10%;
  padding: 15px;
  width: 15%;
  margin-left:41%;
  margin-bottom: 2%;
  font-size: 25px;
  cursor: pointer;
  border-radius: 5px;
  background-color: aquamarine;
}
#Name:focus,#Email:focus,#Phone-no:focus,#Password:focus{
  transition-duration: 1s ;
  border-color: blue;
}
#btn1:hover{
  transition-duration: 0.5s;
  transition: transform(-50%,-50%);
  background-color: red;
  color: white;
}